import * as glm from "gl-matrix";
export class Viewpoint {
    constructor() {
        this.center = glm.vec3.fromValues(0, 0, 0);
        this.eye = glm.vec3.fromValues(0, 1, 3);
        this.up = glm.vec3.fromValues(0, 1, 0);
    }
}
export class TextSpan {
}
export class Scene {
    constructor() {
        this.antipodeAlpha = 0;
        this.baseColor = [0, 0, 0, 0];
        this.cylinderGridlines = 0;
        this.cylinderTransform = glm.mat4.create();
        this.fadeInLuneA = 0;
        this.fadeInLuneB = 0;
        this.fadeInLuneC = 0;
        this.fadeInPolygon = 0;
        this.fadeInTriangle = 0;
        this.greatCircle = 0;
        this.luneAlpha = 0;
        this.luneExpansion = 0;
        this.rotation = 0;
        this.sphereGridlines = 0;
        this.textSpans = [];
        this.triangleExpansion = 0;
        this.viewpoint = new Viewpoint();
    }
}
